export default {
	data() {
		return {
			myindex: 0, //0 企业 1 个人
			type: 0,    // 认证状态
		};
	},
	methods: {
		// 切换认证方式
		bian: function(index) {
			this.myindex = index;
		},
		// 打开新页面
		opennew:function(id){
			uni.navigateTo({
				url: '../'+id+'/'+id
			});
			
		},
		// 复制网址
		copyUrl:function(){
			uni.setClipboardData({
			    data: 'www.jiuwanjiuqian.com',
			    success: function () {
			        
			    }
			});
		},
		// 重新认证
		reset:function(){
			this.type=0
		},
		// 上传图片
		choseimage: function(index) {
			var that = this;
			uni.chooseImage({
				count: 1,
				success: chooseImageRes => {
					that.jiadata[index].images = chooseImageRes.tempFilePaths[0];
					const tempFilePaths = chooseImageRes.tempFilePaths;
					uni.uploadFile({
						url: 'https://www.example.com/upload', //仅为示例，非真实的接口地址
						filePath: tempFilePaths[0],
						name: 'file',
						success: uploadFileRes => {
							console.log(uploadFileRes.data);
						}
					});
				}
			});
		}
	}
};
